@import url("https://fonts.googleapis.com/css2?family=Codystar&family=Krona+One&display=swap");

/*
font-family: 'Codystar', cursive;
font-family: 'Krona One', sans-serif;
*/

a {
  text-decoration: none;
  color: white;
}

.App {
  background-image: url("./Images/Web_back.png");
  /* background-size: cover; */
  background-repeat: repeat;
  background-attachment: fixed;
  background-position: center;
  width: 100%;
  /* height: 700px; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 5rem 0rem;
  /* padding-bottom: 2rem; */
  margin: 0%;
  background-color: #232323;

  font-family: "Codystar", cursive;
  color: white;
  --color1: #222222;
  --color2: goldenrod;
  --color3: white;
}

.MainContainer {
  display: flex;
  flex-direction: column;
  width: 800px;
  align-items: center;
}

.TitleImg img {
  height: 80px;
}

.Die img {
  height: 100px;
}

.Title {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.BuyBtn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 130px;
  margin: 15px;
  margin-top: 30px;
  border: solid var(--color1) 5px;
  color: var(--color1);
  background-color: var(--color2);
  border-radius: 30px;
  font-size: 24px;
  font-weight: bold;
  padding: 10px;
  cursor: pointer;
  transition: 0.3s ease-in-out;
  animation: glowbtn 1s ease-in-out infinite;
}

.BuyBtn:hover {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 130px;
  margin: 15px;
  margin-top: 30px;
  border: solid var(--color2) 5px;
  color: var(--color2);
  background-color: var(--color1);
  border-radius: 30px;
  font-size: 24px;
  font-weight: bold;
  padding: 10px;
  cursor: pointer;
}

.BuyBtn p {
  margin: 0;
}

@keyframes glowbtn {
  0% {
    box-shadow: 0 0 10px gold;
  }
  50% {
    box-shadow: 0 0 0px gold;
  }
  100% {
    box-shadow: 0 0 10px gold;
  }
}

.Table {
  backdrop-filter: blur(5px);
}

.SubTitle {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
}

.FirstRow {
  display: flex;
  flex-direction: row;
}

.SecondRow {
  display: flex;
  flex-direction: row;
}

.TextCell {
  padding: 20px 0px;
  font-family: "Krona One", sans-serif;
  border: solid white 2px;
  box-shadow: 0 0 5px white, 0 0 10px white;
}

.TextElement {
  font-family: "Krona One", sans-serif;
  display: flex;
  flex-direction: row;
  font-size: 24px;
  height: 130px;
}

.TextElement img {
  height: 120px;
  margin-right: 20px;
  margin-left: 20px;
}

/* .TextElement p {
  margin-left: 20px;
} */

.Right {
  width: 590px;
}

.Right h2 {
  margin-left: 25px;
}

.Left {
  width: 100px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.Center {
  width: 150px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.Social {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 30px 0px;
  font-family: "Krona One", sans-serif;
  font-size: 16px;

  /* border: solid red 1px; */
}

.Social img {
  height: 50px;
  margin-right: 20px;
}

.Legendary {
  color: goldenrod;
}

.Rare {
  color: lightseagreen;
}

.common {
  color: white;
}

.ColorCode {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-family: "Krona One", sans-serif;
}

.LegendarySquare {
  border: solid white 2px;
  /* box-shadow: 0 0 5px white; */
  background-color: goldenrod;
  height: 20px;
  width: 20px;
  margin: 5px;
}

.RareSquare {
  border: solid white 2px;
  /* box-shadow: 0 0 5px white; */
  background-color: lightseagreen;
  height: 20px;
  width: 20px;
  margin: 5px;
}

.CommonSquare {
  border: solid white 2px;
  /* box-shadow: 0 0 5px white; */
  background-color: white;
  height: 20px;
  width: 20px;
  margin: 5px;
}
